body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
    background-color: rgba(5,5,5,.678);
   }
  
   .nav {
    content: '';
    display: block;
    clear: both;
  }
  
   .logo {
     margin: 20px;
     color: white;
   }
  
   .logo i {
     color: white;
     font-size: 25px;
     margin-right: 20px
   }
  
   .logo, .menu {
       display:inline-block;
   }
  
   .menu {
     float:right;
     margin: 20px;
   }
  
   .menu ul {
     list-style: none;
     padding-left: 0px;
     margin: 0;
   }
  
   .menu ul li {
       display: inline-block;
       margin-right: 20px;
   }
  
   .menu ul li a {
       text-decoration: none;
       color: white;
       
       padding: 20px 10px 20px 10px;
   }
  
   .menu ul li a:hover {
       background-color: grey;
       color:white;
       -webkit-transition: all 0.5s ease;
       transition: all 0.5s ease;
   }
header {
    background-image: url(/portfolio/static/media/hero.b503d781.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    background-position: center; 
    /* background-attachment: fixed;
    background-position-y: 20px;  */
  }

  header p {
    margin: 0;
  }
 
  .hero-container {
    text-align: center;
    padding-top: 60px;
  }
 
  .btn-work, .btn-contact {
   font-weight: bold;
   border-radius: 20px;
   text-decoration: none;
   padding: 10px 25px;
   display: inline-block;
   font-size: 17px;
  }
 
  .btn-work {
    background-color: #85af4b;
    color: white;
    margin: 15px 10px;
  }
 
  .btn-work:hover {
    background-color: white;
    color:#85af4b;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
 
  .btn-contact {
    background-color: white;
    color: #85af4b;
  }
 
  .btn-contact:hover {
   background-color: #85af4b;
   color:white;
   -webkit-transition: all 0.5s ease;
   transition: all 0.5s ease;
 }
